.exercise-selection-page {
    padding: 0 20px;
}

.exercise-selection-description {
    text-align: justify;
}

.exercise-cards-container {
    display: flex;
    flex-direction: column;
}

.exercise-option-card-container {
    display: flex;
    flex-direction: row;
    margin: 10px 0;
    background: white;
    justify-content: center;
    min-height: 220px;
    /*noinspection CssInvalidFunction*/
    box-shadow: 0 5px 10px rgb(64 64 64 / 20%);
}

.reverse-content {
    flex-direction: row-reverse;
}

@media only screen and (max-width: 430px) {
    .exercise-option-card-container {
        flex-direction: column;
        min-height: 0;
    }

    .reverse-content {
        flex-direction: column;
    }
}

.exercise-option-card-image {
    width: 40%;
    background-position: 50% 50%;
    background-size: cover;
}

.image-1 {
    background-image: url('../belles-baserunning-images/baserunning8.jpeg');
}

.image-2 {
    background-image: url('../belles-baserunning-images/baserunning2.jpeg');
}

.image-3 {
    background-image: url('../belles-baserunning-images/baserunning4.jpeg');
}

.exercise-option-card-content {
    display: flex;
    flex: 1;
    flex-direction: column;
    padding: 20px;
}

.exercise-option-card-title {
    font-size: 18px;
    margin-bottom: 10px;
    font-family: 'LoraBold', -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
}

.exercise-option-card-description {
    font-size: 14px;
    margin-bottom: 10px;
    line-height: 1.6;
}
